import React, {Component} from 'react';


const myNews = [
    {
        author: 'Саша Печкин',
        text: 'В четчерг, четвертого числа...',
        bigText: 'в четыре с четвертью часа четыре чёрненьких чумазеньких чертёнка чертили чёрными чернилами чертёж.'
    },
    {
        author: 'Просто Вася',
        text: 'Считаю, что $ должен стоить 35 рублей!',
        bigText: 'А евро 42!'
    },
    {
        author: 'Гость',
        text: 'Бесплатно. Скачать. Лучший сайт - http://localhost:3000',
        bigText: 'На самом деле платно, просто нужно прочитать очень длинное лицензионное соглашение'
    }
];

class Article extends Component{

    getInitialState(){
        return{
            visible: false
        }
    };

    readmoreClick(e){
        e.preventDefault();
        this.setState({visible:true});
    }

    render(){

        let author = this.props.data.author;
        let text = this.props.data.text;
        let bigText = this.props.data.bigText;

        return(
          <div className="article">
              <h3>{author}</h3>
              <p>{text}</p>
              <a href="#" onClick={this.readmoreClick}>Read more...</a>
              <p>{bigText}</p>
          </div>
        );
    }
}

class News extends Component{
    render(){
        const data = this.props.data;
        let newsTemplate;

        if(data.length){
            newsTemplate = data.map((item,index)=>{
                return(
                    <Article key={index} data={ {author:item.author,text:item.text, bigText:item.bigText} }/>
                );
            });
        }
        else{
            newsTemplate = <p>No news yet</p>
        }

        return(
            <div className="news">
                {newsTemplate}
                <strong>Total news: {data.length}</strong>
            </div>
        );
    }
}

export default class App extends Component {
    render() {
        return (
            <div className="app">
                <h1>News app</h1>
                <News data={myNews}/>
            </div>
        );
    }
}

